'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name base
* @memberof ns
* @readonly
* @type {Namespace}
* @see {@link module:@stdlib/slice/base}
*/
import base from './../base';
setReadOnly( ns, 'base', base );

/**
* @name Slice
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/slice/ctor}
*/
import Slice from './../ctor';
setReadOnly( ns, 'Slice', Slice );

/**
* @name MultiSlice
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/slice/multi}
*/
import MultiSlice from './../multi';
setReadOnly( ns, 'MultiSlice', MultiSlice );

/**
* @name seq2slice
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/slice/seq2slice}
*/
import seq2slice from './../seq2slice';
setReadOnly( ns, 'seq2slice', seq2slice );


// EXPORTS //

export default ns;
